import React, { useEffect, useContext } from 'react';
import './Value.component.scss';
import Icon from '../Icon/Icon.component.jsx';
import { motion, useAnimation } from 'framer-motion';
import { AppContext } from '../../AppContext';

export default function Value({ x, y, id, icon, text }) {
  const { isMD, isPortrait, isLandscape } = useContext(AppContext);

  console.log(process.env.REACT_APP_PUBLIC_URL + icon);
  const xCoord = x;
  const yCoord = y;
  const zCoord = 360 + x;
  const wCoord = -360 + y;

  const animateValueSquare = useAnimation();
  const animateValueItem = useAnimation();

  const inititalWidth =
    isMD && isLandscape ? '70vh' : isMD && isPortrait ? '70vw' : '100vw';
  const expandedWidth =
    isMD && isLandscape ? '120vh' : isMD && isPortrait ? '120vw' : '150vw';

  useEffect(() => {
    animateValueSquare.start({
      rotate: `${zCoord}deg`,
      width: [null, expandedWidth, inititalWidth],
      transition: {
        ease: 'easeInOut',
        duration: 20,
        repeat: Infinity,
      },
    });
    animateValueItem.start({
      rotate: `${wCoord}deg`,
      transition: {
        duration: 20,
        repeat: Infinity,
      },
    });
  }, [
    animateValueSquare,
    animateValueItem,
    zCoord,
    yCoord,
    wCoord,
    inititalWidth,
    expandedWidth,
  ]);

  return (
    <motion.div
      className="value"
      layout
      initial={{
        x: '-50%',
        y: '-50%',
        rotate: `${xCoord}deg`,
        width: [inititalWidth, inititalWidth, inititalWidth],
      }}
      animate={animateValueSquare}
    >
      <motion.div
        layout
        className="value-group"
        initial={{ rotate: `${yCoord}deg` }}
        id={id}
        animate={animateValueItem}
      >
        <Icon
          icon={process.env.REACT_APP_PUBLIC_URL + `${icon}`}
          class={'value-icon'}
        />
        <p className="small">{text}</p>
      </motion.div>
    </motion.div>
  );
}
